import React from 'react'
import { connect } from 'react-redux'
import _ from 'lodash'
import Helmet from 'react-helmet'
import { Link } from 'react-router-dom'
import SignupActions from '../../features/Signup/redux'
import AppConfig from '../../Config/AppConfig'
import { isLoggedIn } from '../../Utils/Utils'
import LoginActions, { LoginSelectors } from 'core/Containers/Login/redux'
import { Grid, Paper, TextField, InputAdornment, Box, Typography, Button } from '@mui/material'
import { useStyles } from './useStyles'
import PersonIcon from '@mui/icons-material/Person'
import EmailIcon from '@mui/icons-material/Email'
import StoreIcon from '@mui/icons-material/Store'
import { LoadingButton } from '@mui/lab'

const basePath = AppConfig.basePath

function PageSignup (props) {
  const { signupRequest, history, loading, loginCheckLogin } = props
  const classes = useStyles()
  const [data, setData] = React.useState({ history, email: '', full_name: '', business_name: '' })
  const [validation, setValidation] = React.useState({ email: '', full_name: '', business_name: '' })

  // componentDidMount
  React.useEffect(() => {
    loginCheckLogin({})
  }, [])

  const handleChange = (event, props) => {
    setData({ ...data, [props]: event.target.value })
  }
  const handleSubmit = () => {
    signupRequest(data)
  }
  const handleGotoLogin = () => {
    history.push('/login')
  }
  if (isLoggedIn(props.isLoggedIn) === true && props.userPrivileges.length > 0) return window.open(`${basePath}${AppConfig.appHomePage}`, '_self', true)
  else console.log('return component')
  return (
    <>
      <Helmet>
        <title>Plink Dashboard</title>
      </Helmet>
      <Grid className={classes.root} container>
        <Paper elevation={8} className={classes.paperBox}>
          <Typography className={classes.pageTitle} fontWeight='fontWeightBold' variant='h4'>Signup</Typography>
          <Box container className={classes.boxRegister}>
            <TextField disabled={loading} onChange={e => handleChange(e, 'email')} label='Email' placeholder='JohnDuke@gmail.com' margin='normal' variant='outlined' InputProps={{ startAdornment: (<InputAdornment position='start'> <EmailIcon /> </InputAdornment>) }} />
            <TextField disabled={loading} onChange={e => handleChange(e, 'full_name')} label='Nama Lengkap' placeholder='John Duke' margin='normal' variant='outlined' InputProps={{ startAdornment: (<InputAdornment position='start'> <PersonIcon />  </InputAdornment>) }} />
            <TextField disabled={loading} onChange={e => handleChange(e, 'business_name')} label='Nama Bisnis' placeholder='Toko kelontong' margin='normal' variant='outlined' InputProps={{ startAdornment: (<InputAdornment position='start'> <StoreIcon /> </InputAdornment>) }} />
            <LoadingButton style={{ marginTop: '2rem', marginBottom: '0.5rem' }} variant='contained' loading={loading} fullwidth onClick={handleSubmit} disabled={data.full_name === '' || data.email === '' || data.business_name === ''}>Submit</LoadingButton>
            <Typography align='center'>OR</Typography>
            <Button style={{ marginTop: '0.5rem' }} variant='outlined' fullwidth onClick={handleGotoLogin}>Login </Button>
          </Box>
        </Paper>
      </Grid>
    </>
  )
}

const mapStateToProps = (state, ownProps) => {
  return {
    loading: state.signup.loading,
    isLoggedIn: LoginSelectors.isLoggedIn(state.login),
    userPrivileges: state.myprofile.user_privileges
  }
}

const mapDispatchToProps = dispatch => {
  return {
    signupRequest: data => dispatch(SignupActions.signupRequest(data)),
    loginCheckLogin: data => dispatch(LoginActions.loginCheckLogin(data))
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(PageSignup)
